import React from "react";
import { useState, Fragment } from "react"
import { storiesOf } from "@storybook/react"

import Modal from "../components/Modal";

const stories = storiesOf("Example", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } });

stories.add("Ex1", () => {
    const [active, setActive] = useState(false)

    const close = () => {
      setActive(!active)
    }
  
    return (
      <Fragment>
        <div className = "container">
          <h1>React Modal</h1>
          <h2>By default</h2>
          <button onClick = {() => setActive(true)}>Open</button>
          <Modal
              open = {active}
              close = {close}
          />
        </div>
      </Fragment>
  
    )
});

stories.add("Ex2", () => {
  const [active, setActive] = useState(false)

  const close = () => {
    setActive(!active)
  }

  const customButton = {
    display: "none"
  }

  return (
    <Fragment>
      <div className = "container">
        <h2>With close button only (showClose = true)</h2>
        <button onClick = {() => setActive(true)}>Open</button>
        <Modal
            open = {active}
            escape = {false}
            outside = {false}
            close = {close}
            showClose = {true}
            styleButton = {customButton}
        />
      </div>
    </Fragment>
  )
});

stories.add("Ex3", () => {
  const [active, setActive] = useState(false)

  const close = () => {
    setActive(!active)
  }

  const customButton = {
    fontSize: "2rem",
    cursor: "pointer",
    fontWeight: "bold",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    background: "#F04329",
    borderColor: "#F04329",
    width: "12rem",
    padding: "1rem",
    borderRadius: ".3rem"
  }

  return (
    <Fragment>
      <div className = "container">
        <h1>React Modal</h1>
        <h2>Modal customized</h2>
        <button onClick = {() => setActive(true)}>Open</button>
        <Modal
            open = {active}
            close = {close}
            styleButton = {customButton}
        />
      </div>
    </Fragment>

  )
});